import React from "react";
import "./About.css";
import jaImg from "../images/ja.jpg";
import GetAppIcon from "@material-ui/icons/GetApp";
import EmojiPeopleIcon from "@material-ui/icons/EmojiPeople";

const About = () => {
  return (
    <section id="about">
      <div className="about-wrap">
        <div className="profile-img">
          <img src={jaImg} alt="Daniel" />
        </div>

        <div className="about-text">
          <div className="aboutme">
            <h2>About Me</h2>
            <p>
              My name is Daniel Žemberi. I'm from Slovakia. I was studying
              digital media graphics on high school. After that I decided to
              study mass media comunication on University of Ss. Cyril and
              Methodius in Trnava. During that time I decided to learn myself
              programming. I started with Python. I was learning couple of
              libraries, but mostly was interested in Flask (for web
              development). Throught Flask I discovered that ReactJS and
              Javascript may be better option. Since then I have been learning
              React from courses and by myself. I take everyday as an
              opportunity to learn something new and improve myself.
            </p>
          </div>

          <div className="contact-details">
            <div className="contact-info">
              <h3>
                <EmojiPeopleIcon />
                Contact Details
              </h3>
              <p> Mgr. Daniel Žemberi</p>
              <p>A. Sládkoviča 24, 919 43 Cífer</p>
              <p>+421 904964447</p>
              <p>danielftwww@gmail.com</p>
            </div>

            <button className="resume">
              <GetAppIcon />
              <a
                href="https://usaupload.com/file/JMg/Zemberi_Daniel-CV.pdf"
                target="_blank"
                rel="noreferrer"
              >
                Download CV
              </a>
            </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
