import React from "react";
import "./Footer.css";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import GitHubIcon from "@material-ui/icons/GitHub";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import { IconButton } from "@material-ui/core";

const Footer = () => {
  return (
    <footer id="footer">
      <a data-easing="easeInCubic" href="#home">
        <IconButton className="footer-uparrow">
          <ArrowUpwardIcon style={{ fontSize: 40, color: "white" }} c />
        </IconButton>
      </a>
      <div className="social">
        <a
          href="https://www.facebook.com/daniel.ftww"
          target="_blank"
          rel="noreferrer"
        >
          <FacebookIcon style={{ fontSize: 40 }} />
        </a>
        <a
          href="https://www.instagram.com/danielftww/"
          target="_blank"
          rel="noreferrer"
        >
          <InstagramIcon style={{ fontSize: 40 }} />
        </a>
        <a href="https://github.com/Fl4wless" target="_blank" rel="noreferrer">
          <GitHubIcon style={{ fontSize: 40 }} />
        </a>
      </div>
      <h3>
        Made by <b>Daniel</b>
      </h3>
    </footer>
  );
};

export default Footer;
