import React, { useState } from "react";
import "./Header.css";
import TypeWriter from "react-typewriter";
import ArrowDropDownCircleOutlinedIcon from "@material-ui/icons/ArrowDropDownCircleOutlined";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import GitHubIcon from "@material-ui/icons/GitHub";
import MenuIcon from "@material-ui/icons/Menu";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";

const Header = () => {
  const [openMenu, setOpenMenu] = useState(false);

  const handleOpenMenu = () => {
    setOpenMenu((prev) => !prev);
  };

  const handleCloseMenu = () => {
    setOpenMenu(false);
  };

  const MenuIconHandle = openMenu ? (
    <HighlightOffIcon
      style={{ color: "white", fontSize: 40 }}
      className="hamburger"
      onClick={handleOpenMenu}
    />
  ) : (
    <MenuIcon
      style={{ color: "white", fontSize: 40 }}
      className="hamburger"
      onClick={handleOpenMenu}
    />
  );

  return (
    <header id="home">
      <nav id="nav-wrap">
        {MenuIconHandle}
        <ul
          id="nav"
          className={`nav ${openMenu && "active"} `}
          onClick={handleCloseMenu}
        >
          <li className="current">
            <a data-easing="easeInCubic" href="#home">
              Home
            </a>
          </li>
          <li>
            <a data-easing="easeInCubic" href="#about">
              About
            </a>
          </li>
          <li>
            <a data-easing="easeInCubic" href="#portfolio">
              Projects
            </a>
          </li>
          <li>
            <a data-easing="easeInCubic" href="#about">
              Contact
            </a>
          </li>
        </ul>
      </nav>

      <div className="row banner">
        <div className="banner-text">
          <h1 className="responsive-headline">
            <TypeWriter typing={0.5}>&lt; Daniel Žemberi /&gt;</TypeWriter>
          </h1>
          <h3>
            <span>React</span> self-taught developer.
          </h3>
          <hr />
          <ul className="social">
            <li>
              <a
                href="https://www.facebook.com/daniel.ftww"
                target="_blank"
                rel="noreferrer"
              >
                <FacebookIcon style={{ fontSize: 40 }} />
              </a>
            </li>
            <li>
              <a
                href="https://www.instagram.com/danielftww/"
                target="_blank"
                rel="noreferrer"
              >
                <InstagramIcon style={{ fontSize: 40 }} />
              </a>
            </li>
            <li>
              <a
                href="https://github.com/Fl4wless"
                target="_blank"
                rel="noreferrer"
              >
                <GitHubIcon style={{ fontSize: 40 }} />
              </a>
            </li>
          </ul>
        </div>
      </div>

      <a data-easing="easeInCubic" href="#about" className="header-arrow">
        <ArrowDropDownCircleOutlinedIcon
          style={{ fontSize: 40, color: "white" }}
        />
      </a>
    </header>
  );
};

export default Header;
